:root {
  --primaryTextColor: #595959;
  --secondaryTextColor: #208275;
  --accentColor: #208275;
  --sidebarColor: #ccffcc6b;
  --sidebarColorHeader: #033c5a;
  --sidebarColorText: #595959;
  --boxShadow: 5px 9px 20px rgba(0, 0, 0, 0.3);
  --timingFn: cubic-bezier(0.4, 0, 0.2, 1);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  background: #f5f7fa;
  /* padding: 40px;  */
  /* max-width: 1200px; */
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
label {
  margin-top: 0px;
  margin-bottom: 0px;
}
@media print {
  html,
  body {
    height: 100vh; /* Use 100% here to support printing more than a single page*/

    width: 1200px;
    margin: 0 !important;
    padding: 0 !important;
    overflow: hidden;
  }
  .wrapper {
    flex-direction: row;
  }
  .sidebar {
    flex-direction: column;
  }
  @page {
    /* size: 210mm 297mm; */
  }
}
